              margin-top: 5px;
              .unit {
                max-width: 135px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
              .num {
                color: #9a9a9a;
                margin-left: 2px;
              }
            }
          }
        }
      }
      .middle {
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        line-height: 50px;
        box-shadow: 0 0 10px 0 rgba(214, 214, 214, 0.5);
        border-radius: 4px;
        margin-top: 10px;
        padding: 0 15px;
        box-sizing: border-box;
        background: #ffffff;
        .left {
          font-size: 16px;
          color: #333333;
          font-weight: bold;
        }
        .right {
          font-size: 12px;
          color: #f43d3d;
        }
      }
      .bottom {
        box-shadow: 0 0 10px 0 rgba(214, 214, 214, 0.5);
        border-radius: 4px;
        margin-top: 10px;
        width: 100%;
        height: 300px;
        padding: 17px 15px 30px 15px;
        box-sizing: border-box;
        background: #ffffff;
        .reason {
          width: 100%;
          height: 96px;
          textarea {
            width: 100%;
            height: 96px;
            font-size: 12px;
            border: 0;
            padding: 0;
            outline: none;
            resize: none;
            line-height: 20px;
          }
          textarea::-webkit-input-placeholder {
            color: #c9c9c9;
            font-size: 14px;
            line-height: 20px;
          }
        }
        .photo_box {
          margin-top: 20px;
          .title {
            width: 100%;
            line-height: 22px;
            font-size: 12px;
            color: #bdbdbd;
            .add {
              font-size: 16px;
              color: #333333;
              font-weight: bold;
            }
          }
          .line {
            width: 345px;
            height: 1px;
            margin-left: -15px;
            background-color: #e5e5e5;
            margin-top: 12px;
          }
          .image-box {
            margin-top: 20px;
          }
        }
      }
    }
  }
  /deep/.image-box {
    font-size: 20px;
    li {
      padding: 3px !important;
      width: 76px !important;
      height: 76px !important;
    }
  }
  /deep/.add-image {
    width: 70px !important;
    height: 70px !important;
  }
}
</style>
